.div_principal {
    width: 430px;
    border: 2px solid #000;
    border-radius: 10px;
    margin-left: 2.3em;
    height: 202px;
    margin-top: 20px;
}

.nome_projeto {
    font-family: arial;
    font-size: 22px;
    text-align: center;
    border: none;
    border-radius: 5px;
    background-color: #000;
    color: #ff6e00;
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
}

.descricao_e_orcamento {
    font-family: arial;
    font-size: 18px;
    margin-left: 20px;
}

.strong {
    color: #ff6e00;
}

.botao {
    height: 25px;
    width: 35px;
    border: none;
    border-radius: 5px;
    background-color: #000;
    color: #fff;
    margin-left: 3px;
}

.botao:hover {
    cursor: pointer;
    color: #ff6e00
}

.icones {
    margin-top: 3px;
}

.div_clicar {
    cursor: pointer;
}

.div_editar {
    margin-left: 6px;
    display: flex;
    justify-content: flex-end;
    margin-right: 20px;
}