.div_principal { 
    height: 900px;
    display: flex;
    flex-direction: column;
}

.div_infos {
    border: 2px solid #000;
    height: 200px;
    width: 800px;
    margin-left: 3em;
    margin-top: 10px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
}

.div_aviso {
    font-family: arial;
    text-align: center;
    margin-right: 30px;
    margin-top: 35px;
}

.div_aviso p {
    font-size: 18px;
}

.div_aviso_op0 {
    opacity: 0;
}

.h1_infos {
    font-family: arial;
    font-size: 45px;
    margin-left: 55px;
}

.p_infos {
    font-family: arial;
    font-size: 22px;
    margin-left: 70px;
}

.p_infos strong {
    color: #ff6e00
}

.h1_metas {
    font-family: arial;
    font-size: 25px;
    margin-left: 70px;
}

.div_metas {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 10px;
    display: flex;
    flex-wrap: wrap;
    border-radius: 10px;

}

.mensagem_sem_metas {
    margin-left: 100px;
    margin-top: 70px;
    font-family: arial;
    font-size: 26px;
}