.div_principal {
    display: flex;
    flex-direction: column;
    height: 900px;
}

.div_principal h2 {
    font-size: 25px;
    margin-left: 50px;
    font-family: arial;
}

.div_projetos {
    display: flex;
    flex-wrap: wrap;
}

.div_titulo_e_botao {
    display: flex;
    justify-content: space-between;
}

.botao_novo_projeto {
    border: none;
    background-color: #000;
    width: 160px;
    height: 50px;
    margin-left: 30px;
    margin-top: 10px;
    margin-right: 30px;
}

.botao_novo_projeto a {
    text-decoration: none;
    color: #fff;
    font-size: 20px;
}

.botao_novo_projeto a:hover {
    color: #ff6e00
}


.mensagem_sem_projetos {
    margin: auto;
    margin-top: 200px;
    font-family: arial;
    font-size: 26px;
}